import React from "react";
import { Navigate } from "react-router-dom";
import { jwtDecode } from "jwt-decode"; // Certifique-se de importar corretamente o jwt-decode

const ProtectedRoute = ({ children, adminOnly = false }) => {
  const token = localStorage.getItem("token");

  // Se no houver token, redireciona para a pgina de login
  if (!token) {
    return <Navigate to="/login" />;
  }

  try {
    // Decodificar o token JWT para verificar a funo do usurio
    const userInfo = jwtDecode(token);

    // Se a rota for somente para admin e o usurio no for admin, redireciona para o Dashboard
    if (adminOnly && userInfo.role !== "admin") {
      return <Navigate to="/dashboard" />;
    }

    // Se passar todas as verificaes, renderiza o componente filho
    return children;
  } catch (error) {
    // Caso o token seja invlido ou no decodificvel, redireciona para login
    localStorage.removeItem("token");
    return <Navigate to="/login" />;
  }
};

export default ProtectedRoute;
