import axios from "axios";
import { toast } from "react-toastify";
import moment from "moment";
// Criao de uma instncia Axios com base na URL base definida em variveis de ambiente
const api = axios.create({
  baseURL: process.env.REACT_APP_API_URL,
});

// Adicionar um interceptor para injetar o token JWT em todas as requisies
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem("token"); // Pega o token do localStorage
    if (token) {
      config.headers.Authorization = `Bearer ${token}`; // Adiciona o token ao cabealho Authorization
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Funo de login
export const login = async (username, password) => {
  try {
    console.log("Tentativa de login com:", { username, password });
    const response = await api.post("/api/auth/login", { username, password });
    console.log("Resposta da API (login):", response.data);
    toast.success("Login realizado com sucesso!");
    return response;
  } catch (error) {
    const errorMessage = error.response
      ? error.response.data.message
      : "Erro ao tentar login. Tente novamente.";
    console.error("Erro durante o login:", errorMessage);
    toast.error(errorMessage); // Exibe a mensagem de erro especfica
    throw error;
  }
};

