// Funo para buscar agendamentos gerais com filtros de data e clnica
export const getAgendamentosGerais = async (data, clinica) => {
  try {
    const params = {};

    // Formata a data corretamente antes de enviar, caso exista
    if (data) {
      const dataFormatada = moment(data).format("YYYY-MM-DD"); // Formata a data para garantir que est no formato correto
      params.data = dataFormatada;
    }

    // Adiciona a clnica aos parmetros de consulta, se fornecida
    if (clinica) {
      params.clinica = clinica;
    }

    // Realiza a requisio GET com os parmetros de consulta
    const response = await api.get("/api/agendamentos", { params });

    return response.data;
  } catch (error) {
    const errorMessage = error.response
      ? error.response.data.message
      : "Erro ao buscar agendamentos gerais. Tente novamente.";
    toast.error(errorMessage);
    throw error;
  }
};
